.step-line {
    
    width: 100%;
    padding: 0px 15px;
    position: relative;
    color: #B6B6B6;
    &:before {
        content: '';
        width: 100%;
        height: 1px;
        background: #B6B6B6;
        position: absolute;
        left: 0;
        top: 16px;
    }



    .line-dot {
        
        border: 2px solid #B6B6B6;
        margin-bottom: 20px;
        &:before {
            background: #B6B6B6;
        }

        @media(max-width: 575px) {
            margin-bottom: 10px;
        }

    }

    .btn {
        margin-top: -80px;

        @media(max-width: 991px) {
            margin-top: 1.5rem;
        }
    }

}

.step-link {

    @media(max-width: 575px) {
        font-size: 15px;
    }
    &.active {
        color: $dark;

        .line-dot {
            border-color: $dark;
            &:before {
                background: $dark;
            }
        }
    }
}

.kyc-text-sm {
    line-height: 1.5;
    color: #B6B6B6;
}

.kys-link {
    
    &.active {
        font-weight: 600;
        color: $dark;
    }

}
