.bx-controls-direction {
    width: 100%;
    z-index: 11;

}

.bx-prev, .bx-next {
    position: absolute;
    width: 78px;
    height: 79px;
    top: 50%;
    transform: translateY(-50%);
}

.bx-prev {
    background: url('../img/arrow-left-b.png') no-repeat 50% 50% / cover;
    left: 0px;
}
.bx-next {
    background: url('../img/arrow-right-b.png') no-repeat 50% 50% / cover;
    right: 0px;
}


@media (max-width: 575px) {
.bx-prev {
    background: url('../img/arrow-left-w.png') no-repeat 50% 50% / cover;
    left: 0px;
}
.bx-next {
    background: url('../img/arrow-right-w.png') no-repeat 50% 50% / cover;
    right: 0px;
}

.bx-next, .bx-prev {
    position: absolute;
    width: 11px;
    height: 18px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
}
		}