body {
	background: $bg-color;
	font-family: $font-main;
	font-weight: $font-weight-light;
}

button {
	&:focus {
		outline: none;
	}
}

a,.btn-link {
	text-decoration: none;
	transition: all .3s;
	&:hover,&:focus {
		text-decoration: none;
		color: $dark;
	}
}

.hr {
	width: 100%;
	height: 1px;
	background: #bbbbbb;
	margin: 15px 0 20px;
}

.img-responsive {
	max-width: 100%;
}

.grey {
	color: #C6C6C6;
}

.nowrap {
	white-space: nowrap;
}

.st0 {
	position: relative;
}

.iframe {

	&-mainVideo {
		position: absolute;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	}
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	    z-index: 100000;
	display: flex;
	align-items: center;
	justify-content: center;
	background:  url('../img/elem-8.png') no-repeat 66% -215px, #ffffff;

	img {
		max-width: 100%;
	}
}


.privat_policy {
    /* border-top: 1px solid #dedede; */
    padding-top: 20px;
    margin-top: 20px;
    text-align: center;
}