.hex-list {
	padding-left: 0;
	margin-bottom: 0;
	list-style: none;

	.list-item-shift {
		padding-right: 140px;

		@media(max-width: 991px) {
			padding-right: 0;
		}
	}

	.list-item {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		width: 100%;

		@media(max-width: 991px) {
			justify-content: center;
		}
	}

	.list-text  {
		@media(max-width: 991px) {
			flex: 0 0 50%;
		}

		@media(max-width: 575px) {
			flex: 0 0 60%;
			font-size: 14px;
			br {
				display: none;
			}
		}
	}

	.list-image {
		max-width: 230px;
		flex: 0 0 230px;
        filter: drop-shadow( -3px 0px 4px #ececec );

		@media(max-width: 575px) {
			max-width: 40%;
			flex: 0 0 40%;
		}
	}

	.list-pattern {
		position: relative;
		&:before {
		    content: '';
		    position: absolute;
		    z-index: -1;
		    width: 911px;
		    height: 1205px;
		    background: url(../img/elem-5.png?v=1.1.12) 50% 50%/cover no-repeat;
		    right: 95px;
		    top: 50%;
		    margin-top: -485px;
		}
	}
}

.num-list {
	margin-bottom: 0;
	list-style: none;
	padding-left: 0;
	font-size: 16px;

	@media(max-width: 991px) {
		font-size: 15px;
	}

	.list-item {
		display: flex;
		align-items: flex-start;
	}

	.list-num {
		width: 60px;
		height: 60px;
		border-radius: 50%;
		background: #EBEBEB;
		color:$dark;
		text-align: center;
		line-height: 60px;
		margin-right: 40px;
		flex: 0 0 60px;

		@media(max-width: 991px) {
			width: 50px;
			height: 50px;
			flex: 0 0 50px;
			line-height: 50px;
			margin-right: 20px;
		}

		@media(max-width: 575px) {
			width: 35px;
			height: 35px;
			flex: 0 0 35px;
			line-height: 35px;
			margin-right: 15px;
		}
	}
}