.navbar {

	padding: 0rem  0rem;
	@media(max-width: 575px) {
		padding: 0 0;
	}
	
	&-bg {
		&-light {
			background: transparent;
		}
		&-dark {
			background: $white;
		}
	}

	&-light {
		background: transparent;
		border-bottom: 1px solid $white;

		.navbar-toggler-icon {
			background: $white;

			&:before, &:after {
				background: $white;
			}
		}

		.navbar-nav {
			.nav-link {
				color: $white;
				&:hover {
					color: $dark;
				}
			}
		}

		.nav-link-circle {
			background: $white;
			color: $dark!important;
		}

		.navbar-toggler {
			border-color: transparent;
		}

	}

	&-dark {
		border: none;
		background: $white;
		box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);

		.navbar-toggler-icon {
			background: $dark;

			&:before, &:after {
				background: $dark;
			}
		}

		.navbar-nav {
			.nav-link {
				color: $dark;
				&:hover {
					color: #343434;
				}
			}
		}

		.nav-link-circle {
			background: $dark;
			color: $white!important;
		}

		.navbar-nav {
			.nav-link {
				&:hover,&:focus {
		    		color: $dark;
		    	}
			}
		}

	}

	&-brand {
		padding: .75rem 0;
		margin-bottom: 0;
		
		img {
			height: 70px;

			@media(max-width: 991px) {
				height: 40px;
			}
		}

		@media(max-width: 991px) {
			padding: 0.65rem 0;
		}
	}

	&-inner {

		padding: 25px 15px;
		max-height: calc(100vh - 120px);
		overflow-y: auto;

		@media(max-width: 991px) {
			margin: 0 -15px;
			max-height: calc(100vh - 192px);
		}
	
		&-list {
			padding-left: 0;
			list-style: none;
			margin-bottom: 0;

			li {
				margin-bottom: 10px;
			}

			a {
				color: #444;
			}
		}

	}

	&-list {

		&-small {
			li {
				font-size: 14px;
				margin-bottom: 7px;
			}
		}
		
		&-default {
			li {
				margin-bottom: 14px;
				font-size: 19px;
				@media(max-width: 1199px) {
					font-size: 18px;
				}
				@media(max-width: 991px) {
					font-size: 16px;
				}
			}
		}

		&-large {
			font-size: 30px;
    		margin-top: -7px;
    		@media(max-width: 1199px) {
				font-size: 24px;
				margin-top: -2px;
			}
			@media(max-width: 767px) {
				margin-top: 0;
				font-size: 21px;
			}
		}

		&-row {
			li {
				width: 100%;
				margin-bottom: 5px;
			}
			p {
				margin-bottom: 5px;
			}
		}

	}
	
	&-collapse {
		// padding-left: 120px;
		flex-grow: 0;

	}

	&-expand-xl {
		flex-wrap: wrap;
		.navbar-nav {
			.nav-link {
				padding: 0 11px;
				@media(max-width: 1199px) {
					padding: 0 5px;
				}
			}
		}
		&>.container {
			@media(max-width: 991.98px) {
				padding-left: 15px;
				padding-right: 15px;
			}
		}
	}

	&-toggler {
		padding: 0;

		&-icon {
			width: 32px;
			height: 3px;
			position: relative;
			top: -1px;

			&:before, &:after {
				content: '';
				width: 32px;
				height: 3px;
				position: relative;
				display: block;
				transition: all .3s;
			}

			&:before {
				top: 12px;
			}

			&:after {
				top: -15px;
			}

			&.open {
				height: 0;
				&:before {
					top: 0px;
					transform: rotate(45deg);
				}

				&:after {
					top: -3px;
					transform: rotate(-45deg);
				}
			}
		}
	}
	
	.dropdown-menu {
		position: absolute;
	}

	
}

.nav-link {
	font-weight: 600;
	font-size: 16px;
	letter-spacing: 0px;
	

	&-circle {
		display: inline-block;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		line-height: 32px;
		text-align: center;
		padding: 0!important;
		font-size: 12px;
		font-weight: 600;
	}
}

.dropdown-menu {
	min-width: auto;
	left: -15px;
	border-radius: 50px;
	overflow: hidden;
}