.scale {
    width: 100%;
    height: 60px;
    border: 2px solid $white;
    border-radius: 40px;
    padding: 6px;
    overflow: hidden;
    position: relative;

    &-line {
        // background: linear-gradient(to left,#c8227d 0,#991a4e 100%);
        background: linear-gradient(to left,#2e85b7 0,#114868 100%);
        position: relative;
        height: 100%;
        left: 0;
        width: 30%;
        line-height: 48px;
        padding-left: 20px;
        min-width: 120px;
        border-radius: 40px 0 0 40px;
    }
}