.col-1-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 0 0 20%;
    max-width: 20%;
}

.col-md-1-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    @media (min-width: 768px) {
        flex: 0 0 20%;
        max-width: 20%;
    }
}

.col-lg-1-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    @media (min-width: 992px) {
        flex: 0 0 20%;
        max-width: 20%;
    }
}

.col-lg-2-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    @media (min-width: 992px) {
        flex: 0 0 40%;
        max-width: 40%;
    }
}

.col-lg-3-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    @media (min-width: 992px) {
        flex: 0 0 60%;
        max-width: 60%;
    }
}
.col-lg-4-5 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    @media (min-width: 992px) {
        flex: 0 0 80%;
        max-width: 80%;
    }
}


.offset {
    
    &-lg {
        
        &-1-5 {
            @media (min-width: 992px) {
                margin-left: 10%;    
            }
        }
        
        &-2-5 {
            @media (min-width: 992px) {
                margin-left: 20%;    
            }
        }
        
        &-3-5 {
            @media (min-width: 992px) {
                margin-left: 30%;    
            }
        }
        
        &-4-5 {
            @media (min-width: 992px) {
                margin-left: 40%;    
            }
        }

    }

}