.blog {
    
    &-item {
        position: relative;
        background: #ececec;
    }

    &-half {
        width: 100%;
        height: 145px;
    }

    &-full {
        width: 100%;
        height: 290px;

        @media(max-width: 1199px) {
            height: 240px;
        }

        @media(max-width: 991px) {
            height: 360px;
        }

        @media(max-width: 767px) {
            height: 270px;
        }

        @media(max-width: 575px) {
            height: 50vw;
        }
    }

    &-link {
        display: inline-block;
        width: 100%;
        height: 100%;
        position: relative;

        .video-play--btn {
            width: 100px;

            &:hover {
                width: 115px;
            }
        }
    }

    &-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

}