.section-docs {
  
}

.docs-wrap {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-top: 10px;

}

.doc-item {
    margin: 0 15px;
    margin-bottom: 85px;
    text-align: center;
    @media(max-width: 991px) {
      margin: 0;
    }
    
}

.doc-link {
    display: inline-block;
    flex: 0 0 187px;
    max-width: 187px;
    height: 267px;
    position: relative; 

    @media(max-width: 1199px) {
      flex: 0 0 155px;
      max-width: 155px;
      height: 219px;
    }

    @media(max-width: 575px) {
      margin: 0;
    }

    &:hover {
        transform: scale(1.02);
        &:before {
            background-image: linear-gradient(to left, #92b0ff, #92b0ff);
        }
        &:after {
            background: #ababe4;
        }
    }

    img {
        width: 100%;
        height: 100%;
        position: relative;
        
    }
}

.doc-link:before, .doc-link:after {
  content: "";
  position: absolute;
}
.doc-link:before {
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background-image: linear-gradient(to left, #92b0ff, #c0e1fe);
  z-index: -2;
  tranition: all .3s;
}
.doc-link:after {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #ffffff;
  z-index: -1;
}
.doc-title {
    padding-top: 25px;
    font-size: 21px;
    font-weight: bold;
}