.token {

    &-percent {
        float:right;
        display: inline-block;
        font-weight: bold;
        position: relative;
        font-size: 120%;

        &:before {
            content: '';
            display: block;
            position: absolute;
            left: 200%;
            top: 50%;
            transform: translateY(-50%);
            width: 500px;
            height: 1px;
            background: #000;

            @media (max-width: 1199px) {
                width: 410px;
            }
            @media (max-width: 991px) {
                display: none;
            }
        }
    }
    
    &-title {
        font-size: 39px;

        @media(max-width: 767px) {
            font-size: 34px;
        }

        @media(max-width: 575px) {
            font-size: 7.5vw;
        }
        .mark {
            font-weight: 600;
        }
    }

    &-text {
        font-size: 19px;

        @media(max-width: 767px) {
            font-size: 17px;
        }

        @media(max-width: 575px) {
            font-size: 3.5vw;
        }
    }
    
    &-info {
        
        &-list {
            padding-left: 0;
            margin-bottom: 0;
            list-style: none;
            font-size: 19px;

            @media (max-width: 1199px) {
                font-size: 17px;
            }

            @media(max-width: 575px) {
                font-size: 3.5vw;
                margin-right: -35px;
            }

            &.list-reverse {

                @media(max-width: 991px) {
                    margin-left: -55px;
                }

                @media(max-width: 767px) {
                    margin-left: -100px;
                }

                @media(max-width: 575px) {
                    margin-left: -25vw;
                    margin-right: 0;
                }

                .list-item {
                    text-align: left;
                    max-width: 320px;
                }
                .token-percent {
                    padding-right: 40px;
                    float: left;
                    @media(max-width: 767px) {
                        float: right;
                    }
                    @media(max-width: 575px) {
                        padding-right: 0;
                    }
                    &:before {
                        left: initial;
                        right: 140%;
                        width: 450px;

                        @media (max-width: 1199px) {
                            width: 335px;
                        }
                    }
                }
            }
                
            .list-item {
                display: inline-block;
                width: 100%;
                max-width: 280px;

                @media (max-width: 1199px) {
                    max-width: 235px;
                }
                @media (max-width: 991px) {
                    white-space: no-wrap;
                }

                &:not(:last-child) {
                    margin-bottom: 5px;
                    @media (max-width: 1199px) {
                        margin-bottom: 2px;
                    }
                }

                &:nth-child(1) {
                    .token-percent {
                        color: #c0e0fd;
                        &:before {
                            background: #c0e0fd;
                        }
                    }
                }

                &:nth-child(2) {
                    .token-percent {
                        color: #accffd;
                        &:before {
                            background: #accffd;
                        }
                    }
                }

                &:nth-child(3) {
                    .token-percent {
                        color: #99bcff;
                        &:before {
                            background: #99bcff;
                        }
                    }
                }

                &:nth-child(4) {
                    .token-percent {
                        color: #85abff;
                        &:before {
                            background: #85abff;
                        }
                    }
                }

                &:nth-child(5) {
                    .token-percent {
                        color: #7199ff;
                        &:before {
                            background: #7199ff;
                        }
                    }
                }

                &:nth-child(6) {
                    .token-percent {
                        color: #7199ff;
                        &:before {
                            background: #7199ff;
                        }
                    }
                }
            }

        }

    }

}