.title{

	&-xl {
		
		font-weight: $font-weight-light;
		font-size: 47px;
	    line-height: 1.3;
	    margin-bottom: 0;

	    @media(max-width: 575px) {
			font-size: 32px;
	    }

	    .mark {
			font-weight: $font-weight-bold;
		}
	}

	&-lg {
		line-height: 1.3;
		font-size: 36px;
		font-weight: $font-weight-light;
		margin-bottom: 0;

		.mark {
			font-weight: $font-weight-bold;
		}
	}

	&-md {
		line-height: 1.3;
		font-size: 24px;
		font-weight: $font-weight-light;
		margin-bottom: 0;

		.mark {
			font-weight: $font-weight-bold;
		}
	}

	&-sm{
		line-height: 1.3;
		font-size: 19px;
		font-weight: $font-weight-light;
		margin-bottom: 0;

		.mark {
			font-weight: $font-weight-bold;
		}
	}

	&-presale {

		text-transform: uppercase;
		font-size: 19px;
		line-height: 1;

		@media (max-width: 1199px) {
			font-size: 19px;
		}

		@media (max-width: 575px) {
			font-size: 17px;
			margin-bottom: 10px;
		}
		.mark {
			color: #d52a77;
			font-weight: 600;
			font-size: 24px;
		}
	}
}

.text {

	&-sm {
	    font-size: 13px;
		line-height: 2;
		margin-bottom: 0;
	}
	&-default {
	    font-size: 16px;
		margin-bottom: 0;
	}
	&-md {
	    font-size: 19px;
		margin-bottom: 0;

		.mark {
    		font-weight: 500;
    		color: #d52a77;
		}
	}
	&-lg {
	    font-size: 23px;
		margin-bottom: 0;

		.mark {
    		font-weight: 500;
    		color: #d52a77;
		}

		&-hr {
			font-weight: 400;
			@media(min-width: 992px) {
				margin-bottom: 1rem;
				padding-bottom: 1rem;
				position: relative;
				font-weight: 300;
			}
			&:before {
				
				@media(min-width: 992px) {
					content: '';
					position: absolute;
					width: 95%;
					margin-left: -5%;
					left: 0;
					bottom: 0;
					height: 1px;
					background: #dedede;
				}
			}
		}
	}

	&-xl {

		font-size: 28px;
		.mark {
    		font-weight: 500;
    		color: #d52a77;
		}
	}

	&-grey {
		color: #A5A5A5;
	}

	&-elipsis {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&-referal {
		display: block;
		width: 100%;
		overflow: auto;
	}

	&-invite {
		font-size: 25px;
		line-height: 1;

		@media (max-width: 1199px) {
			font-size: 21px;
		}
		
		&--small {
			font-size: 14px;
			color: #d52a77;
			.mark {
				font-size: 16px;
	    		font-weight: 500;

			}
		}

		.mark {
			font-size: 46px;
    		font-weight: 500;
    		color: #d52a77;

    		@media (max-width: 1199px) {
    			font-size: 32px;
    		}
		}
	}

}

.shift {
	&-r--180 {
		margin-right: -180px;
	}
	&-l--230 {
		margin-left: -230px;
	}

	&-t--40 {
		margin-top: -40px;
		@media ( max-width: 991px ) {
			margin-top: 0;
		}
	}

	&-t-200{
		margin-top: 200px;

		@media ( max-width: 1199px ) {
			margin-top: 70px;
		}

		@media ( max-width: 991px ) {
			margin-top: 0px;
		}
	}

	&-l-280 {
		margin-left: 280px;
		@media ( max-width: 991px ) {
			margin-left: 0;
		}
	}
}

.link {
	
	&-lg {
		&-hr {
			@media ( max-width: 991px ) {
				width: 1px;
				background: $dark;
				height: 15px;
				position: relative;
				margin: 0 4px;
				vertical-align: middle;
			}

			@media ( max-width: 575px ) {
				margin: 0 1px;
			}
		}
	}

	&-dark {
		color: $dark;
		&:hover {
			color: #92b0ff;
		}
	}

	&-red {
		color: #c8227d;
		&:hover {
			color: #92b0ff;
		}
	}

}

.lh-normal {
	line-height: 1.8;
}