.wrapper {
	overflow: hidden;
}

.section {
	
	position: relative;

	&-mt-200 {
		margin-top: -200px;
	}

	&-mb-100 {
		margin-bottom: 100px;
	}

	&-mb-200 {
		margin-bottom: 200px;
	}

	&-pt-100 {
		padding-top: 100px;
	}

	&-pt-150 {
		padding-top: 150px;
	}

	&-pt-200 {
		padding-top: 200px;
	}

	&-pb-170 {
		padding-bottom: 170px;
	}

	&-pb-50 {
		padding-bottom: 50px;
	}

	&-pt-50 {
		padding-bottom: 50px;
	}

	&-pb-100 {
		padding-bottom: 100px;
	}

	&-rounded {
		position: relative;
		z-index: 5;
		margin-top: -200px;
		padding-top: 100px;
		padding-bottom: 100px;
		// padding-bottom: 170px;
		@media (max-width: 575px) {
			padding-top: 90px;
			margin-top: -100px;
		}

		&.section-reverse {
			@media (max-width: 991px) {
				margin-top: -200px;
				padding-top: 300px;
				padding-bottom: 300px;
				z-index: 0;
			}
			
			@media (max-width: 575px) {
				padding-top: 195px;
				padding-bottom: 175px;
				margin-top: -100px;
			}
		}
	}

	&-middle {
		margin-top: -200px;
		padding-top: 300px;
		padding-bottom: 300px;
		@media (max-width: 575px) {
			padding-top: 195px;
			padding-bottom: 175px;
			margin-top: -100px;
		}

		&.section-reverse {
			
			@media (max-width: 991px) {
				position: relative;
				z-index: 5;
				margin-top: -200px;
				padding-top: 100px;
				padding-bottom: 100px;
				// padding-bottom: 170px;
			}
			@media (max-width: 575px) {
				padding-bottom: 100px;
				padding-top: 90px;
				margin-top: -100px;
			}
			
		}

		&--first {
			margin-top: 0;
			padding-top: 100px;

		}
	}

	&-round {
		position: absolute;
		z-index: 1;
		height: 100%;
		width: 3100px;
		margin-left: -1550px;
		top: 0;
		left: 50%;
		overflow: hidden;
		background: #ffffff;

		&-full {
			border-radius: 50%;
		}

		&-bottom {
			border-radius: 0 0 50% 50% ;
		}

		&-top {
			border-radius: 50% 50% 0 0;
		}

		&--dark {
			background: #F3F3F5;
		}

		&--white {
			background: #ffffff;
		}
		
	}

	&-content {
		position: relative;
		z-index: 2;
	}

	&-what-is-viarium {
		background:  url('../img/elem-1.png?v=1.1.20') no-repeat 62% -215px, url('../img/bg-1.png?v=1.1.20') no-repeat 15% 65%, #ffffff;

		@media(max-width:991px) {
			background: url('../img/pattern-viarium-1-m.png?v=1.1.20') no-repeat 50% 0%, url('../img/pattern-viarium-2-m.png?v=1.1.20') no-repeat 49% 100%, #ffffff;
		}
	}

	&-why-blockchain {
		background:  url('../img/elem-4.png?v=1.1.20') no-repeat 66% -215px, #ffffff;
		@media(max-width:991px) {
			background: url('../img/pattern-team-m.png?v=1.1.20') repeat 50% 50%,#fafafb; 
		}
	}

	&-roadmap {
		background: url('../img/elem-6.png?v=1.1.20') no-repeat 1442px 256px;
		position: relative;
		@media(max-width:991px) {
			background: url('../img/elem-6.png?v=1.1.20') no-repeat 1442px 256px;
		}

        .hidden-block {
            display: none;
        }
        .trigger {
            color: #007bff;
            display: inline-block;
            &:hover {
                color: #007bff;
                text-decoration: underline;
            }
        }
	}

	&-team {
		// background: url('../img/elem-6.png?v=1.1.20') no-repeat 1442px -716px;

		&-bg {
			background: url('../img/elem-7.png?v=1.1.20') no-repeat 10% 100%, #fff;

			@media(max-width:991px) {
				background: url('../img/pattern-team-m.png?v=1.1.20') repeat 50% 50%,#fafafb; 
			}
		}
	}

	&-information {
		position: relative;
		background: url('../img/elem-2.png?v=1.1.20') no-repeat -20% 100%, url('../img/elem-3.png?v=1.1.20') no-repeat 120% 100%;
	}

	&-token {
		background: url('../img/elem-8.png?v=1.1.20') no-repeat -800px 100%, #fafafb;

		&-reverse {
			background: url('../img/elem-8.png?v=1.1.20') no-repeat 100% 100%, #ffffff;
		}
	}

	&-footer {
		font-size: 17px;
		padding-bottom: 50px!important;

	}

	&-map {
		background: url('../img/elem-9.png?v=1.1.20') no-repeat 100px 0%;
		@media (max-width: 991px) {
			background: initial;
		}
	}

	&-blog {
		background: url('../img/elem-2.png?v=1.1.20') no-repeat 10% 0%, url('../img/elem-6.png?v=1.1.20') no-repeat 100% 70%, #ffffff;
	}

	&-partners {
		    background: url(../img/bg-1.png?v=1.1.20) 15% 65% no-repeat;
	}

	&-authorization, &-account {
		padding-top: 150px;
		min-height: calc(100vh - 245px);
		background: #ffffff;
	}

	&-account {
		background: transparent;
	}
}

.hex {
	&:before {
		content: '';
		position: absolute;
		z-index: 1;
	}

	&-1 {
		
		&:before {
			width: 472px;
			height: 442px;
			top: -160px;
			left: -370px;
			background: url('../img/hex/hex_1.png?v=1.1.20') no-repeat 50% 50% / cover;
			@media(max-width: 1199px) {
				width: calc(472px / 1.3);
				height: calc(442px / 1.3);
			}
		}
	}

	&-2 {
		
		&:before {
			width: 509px;
		    height: 442px;
		    bottom: -260px;
		    right: -85px;
			background: url('../img/hex/hex_2.png?v=1.1.20') no-repeat 50% 50% / cover;

			@media(max-width: 1199px) {
				width: calc(509px / 1.3);
				height: calc(442px / 1.3);
			}

			@media(max-width: 575px) {
				width: calc(509px / 2);
				height: calc(442px / 2);
			}
		}

		&-a {
			&:before {
				width: 509px;
				height: 442px;
				bottom: -350px;
	    		right: -70px;
				background: url('../img/hex/hex_2.png?v=1.1.20') no-repeat 50% 50% / cover;

				@media(max-width: 1199px) {
					width: calc(509px / 1.3);
					height: calc(442px / 1.3);
				}

				@media(max-width: 575px) {
					width: calc(509px / 2);
					height: calc(442px / 2);
					bottom: -200px;
				}
			}
		}
	}

	&-3 {
		
		&:before {
			width: 221px;
			height: 173px;
			bottom: -110px;
    		right: 50%;
    		margin-right: -110px;
			background: url('../img/hex/hex_3.png?v=1.1.20') no-repeat 50% 50% / cover;
		}
	}

	&-4 {
		
		&:before {
			width: 445px;
			height: 409px;
			top: -175px;
			left: -320px;
			background: url('../img/hex/hex_4.png?v=1.1.20') no-repeat 50% 50% / cover;
			@media(max-width: 1199px) {
				width: calc(445px / 1.3);
				height: calc(409px / 1.3);
			}
		}
	}

	&-5 {
		
		&:before {
			width: 509px;
			height: 442px;
			bottom: -330px;
    		left: -255px;
			background: url('../img/hex/hex_5.png?v=1.1.20') no-repeat 50% 50% / cover;
			@media(max-width: 1199px) {
				width: calc(509px / 1.3);
				height: calc(442px / 1.3);
			}
		}
	}

	&-6 {
		
		&:before {
			width: 221px;
			height: 173px;
			top: -170px;
    		right: 50%;
    		margin-right: -110px;
			background: url('../img/hex/hex_6.png?v=1.1.20') no-repeat 50% 50% / cover;
		}
	}

	&-7 {
		
		&:before {
			width: 221px;
			height: 173px;
			top: -65px;
    		right: 50%;
    		margin-right: -110px;
			background: url('../img/hex/hex_7.png?v=1.1.20') no-repeat 50% 50% / cover;
			@media(max-width: 1199px) {
				width: calc(221px / 1.3);
				height: calc(173px / 1.3);
				margin-right: calc(-110px / 1.3);
			}
		}

		&-bottom {
			&:before {
				width: 221px;
				height: 173px;
				bottom: -85px;
	    		right: 50%;
	    		margin-right: -110px;
				background: url('../img/hex/hex_7.png?v=1.1.20') no-repeat 50% 50% / cover;
				@media(max-width: 1199px) {
					width: calc(221px / 1.3);
					height: calc(173px / 1.3);
					margin-right: calc(-110px / 1.3);
				}
			}
		}
	}
}
