.account {
    &-menu {
        .menu-list {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: nowrap;
            padding-left: 0;
            margin-bottom: 0;
            list-style: none;
            width: 100%;
            border-bottom: 1px solid #C4C4C6;
            @media (max-width: 767px) {
                flex-wrap: wrap;
            }
        }
        .list-item {
            flex: 0 0 20%;
            max-width: 20%;
            text-align: center;
            @media (max-width: 767px) {
                flex: 0 0 33%;
                max-width: 33%;
            }
            &--br {
                @media (max-width: 767px) {
                    flex: 0 0 50%;
                    max-width: 50%;
                }
            }
            &.active {
                .menu-link {
                    box-shadow: 0 4px 0 0px #7F81D3;
                }
            }
            &.disable {
                .menu-link {
                    color: #C4C4C6;
                }
            }
        }
        .menu-link {
            color: $dark;
            display: block;
            padding: 10px;
            &:hover {
                box-shadow: 0 4px 0 0px #7F81D3;
            }
        }
    }
}

.panel {
    width: 100%;
    background: $white;
    border: 1px solid #EAEAEC;
    margin-bottom: 35px;
    padding: 0px 30px;
    @media(max-width: 575px) {
        padding: 0 20px;
    }
    &-row {
        position: relative;
        margin: 0 -30px;
        padding: 30px 30px;
        @media(max-width: 575px) {
            padding: 20px 20px;
            margin: 0 -20px;
        }
        min-height: 110px;
        font-size: 32px;
        @media (max-width: 1199px) {
            min-height: 80px;
            padding: 20px 30px;
            font-size: 24px;
        }
        &--line {
            border-bottom: 1px solid #EAEAEC;
        }
        &--title {
            border-bottom: 1px solid #EAEAEC;
            padding: 10px 30px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            flex-direction: row;
            @media (max-width: 575px) {
                padding: 10px 10px;
            }
            img {
                @media (max-width: 575px) {
                    max-width: 30px;
                }
            }
        }
        .title-md {
            line-height: 1;
            @media (max-width: 575px) {
                font-size: 21px;
            }
        }
    }
}