.form {

    &-text-separate {
        display: block;
        position: relative;
        text-align: center;

        &:before,&:after {
            content: '';
            height: 1px;
            width: 40%;
            background: $dark;
            position: absolute;
            top: 50%;
        }

        &:before {
            left: 0;
        }

        &:after {
            right: 0;
        }
    }

    &-group {
        margin-bottom: 1.35rem;
    }

    &-control {
        border-radius: 2rem;
        padding: .750rem 2rem;
        font-weight: 300;
        border: 1px solid $dark;

        @media(max-width: 575px) {
            padding: .450rem 1.4rem;
        }

        &:focus {
            box-shadow: 0 0 0 1px #80bdff
        }

        &::-webkit-inner-spin-button {
          display: none;
        }
    }

    &-grey {
        .form-control {
            border: 1px solid #C4C4C6;
        }

        .custom-select {
            border: 1px solid #C4C4C6;
            background-color: $white;
        }
    }

}


.input-group {
    display: flex;
    flex-wrap: nowrap;

    .form-control {
        border-radius: 2rem  0 0 2rem;
        border-right: none;
    }

    .btn {
        border-radius: 0 2rem 2rem 0;
        img {
            max-height: 18px;
        }
    }
}

.was-validated {
    .invalid-feedback {
        display: block;
    }

    .form-control {
        &:invalid {
            box-shadow: 0 0 0 1px #dc3545
        }

        &:valid {
            box-shadow: 0 0 0 1px #28a745
        }
    }

    .custom-select {

        &:valid {
            box-shadow: 0 0 0 1px #28a745
        }

        &:invalid {
            box-shadow: 0 0 0 1px #dc3545
        }
    }
}

.invalid-feedback {
    color: #c8227d;
}

.custom {

    &-select {
        display: inline-block;
        width: 100%;
        height: 50px;
        padding: .750rem 2rem;
        line-height: 1.5;
        color: #495057;
        vertical-align: middle;
        background-size: 8px 10px;
        border: 1px solid $dark;
        background: #fff, url('../img/appearance.svg') no-repeat 90% 1rem;
        background-size: 12px 15px;
        border-radius: 2rem;
        appearance: none;
        color: #9E797C;
        font-weight: 300;
        &:focus {
            outline: none;
            box-shadow: 0 0 0 1px #80bdff
        }

        @media(max-width: 575px) {
            height: 40.3px;
            padding: .450rem 1.4rem;
            background-position: 90% 12px;
        }
    }

}

.custom-radio .custom-control-input:checked~.custom-control-label::before {
    background-color: #92b0ff;
}

.custom-radio--dark .custom-control-label {
    font-size: 16px;
    line-height: 1;
    padding-top: 4px;
}

.custom-radio--dark{
    padding-left: 2.5rem;

}

.custom-radio--dark .custom-control-label::before {
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: 2px solid #B6B6B6;
    left: -2.5rem;
    top: -0.25rem;

}

.custom-radio--dark .custom-control-label::after {
    width: 1.5rem;
    height: 1.5rem;
    background: #B6B6B6;
    border-radius: 50%;
    left: -2.25rem;
    top: 0rem;

}

.custom-radio--dark{
    min-height: 2rem;

}

.custom-radio--dark .custom-control-input:checked~.custom-control-label::after {
    background-image: none;
    background-color: $dark;
}
.custom-radio--dark .custom-control-input:checked~.custom-control-label::before {
    background-image: none;
    background-color:transparent;
    border-color: $dark;
}

.custom-radio--dark .custom-control-input:focus~.custom-control-label::before {
    box-shadow: none;
}

#subscriptionForm .btn {
padding: 10px 35px;
}