.section-team {

    .bx-prev {
        left: -70px;
        top: 160px;
        transform: translateY(0);
    }

    .bx-next {
        right: -70px;
        top: 160px;
        transform: translateY(0);
    }

    @media(max-width: 1340px) {
        .bx-prev {
            left: -15px;
        }

        .bx-next {
            right: -15px;
        }
    }

    @media(max-width: 575px) {
        .bx-prev {
            left: 0;
        }

        .bx-next {
            right: 0;
        }
    }

    @media(max-width: 575px) {
        .bx-prev {
            left: 0;
            width: 57px;
            height: 56px;
        }

        .bx-next {
            right: 0;
            width: 57px;
            height: 56px;
        }
    }
}

.team-slide {
    padding: 0 60px;

    @media(max-width: 575px) {
        padding: 0 0;
    }
}

.member {
    &-group {
        position: relative;

        @media(max-width: 991px) {
            width: 100%;
            max-width: 310px;
            display: inline-block;
            
        }
    }

    &-image {
        position: relative;
        z-index: 2;
        width: 100%;
        max-width: 230px;
        display: inline-block;

        img {
            width: 100%;
            max-width: 230px;

            @media(max-width: 575px) {
                max-width: 190px;
            }
        }
    }

    &-social {
        position: absolute;
        z-index: 3;
        right: 0;
        bottom: 0;
        text-align: right;

        @media(max-width: 575px) {
            right: 25px;
        }

        a {
            display: block;

            &:nth-child(1) {
                padding-bottom: 20px;

                @media(max-width: 1199px) {
                    margin-right: -20px;
                }

                @media(max-width: 991px) {
                    margin-right: 0px;
                }

                @media(max-width: 575px) {
                    padding-bottom: 5px;
                }
            }

            &:nth-child(2) {
                padding-bottom: 10px;
                padding-right: 20px;
            }
            
            img {
                @media(max-width: 575px) {
                    width: 100%;
                    max-width: 35px;
                }
            }

        }
    }

    &-name {
        font-size: 24px;
        font-weight: 400;
        line-height: 1;
        margin-bottom: 10px;

        @media(max-width: 1199px) {
            font-size: 22px;
        }
    }

    &-position {
        font-size: 19px;
        font-weight: 300;
        line-height: 1;
        margin-bottom: 10px;
        @media(max-width: 1199px) {
            font-size: 18px;
        }
    }

    &-text {
        font-size: 14px;
        font-weight: 300;
        line-height: 1.9;
        @media(max-width: 1199px) {
            line-height: 1.6;
        }
    }
}

