.video-poster {
    width: 100%;
    height: 100%;
    position: relative;
    height: 80vw;
    max-height: 1080px;
    
}

.video-play {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    &--1 {
        background: url('../img/poster/video-poster--1.jpg') no-repeat 50% 50% / cover; 
    }
    &--2 {
        background: url('../img/poster/video-poster--2.jpg') no-repeat 50% 50% / cover; 
    }
    &--3 {
        background: url('../img/poster/video-poster--3.jpg') no-repeat 50% 50% / cover; 
    }
    &--4 {
        background: url('../img/blog/1_oCdYNF_i4VAYIsdfb4U2AP7Q.jpg') no-repeat 50% 50% / cover; 
    }
}

.video-play--btn {
    position: absolute;
    z-index: 2;
    width: 200px;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    transition: all .3s;

    @media(max-width: 575px) {
        width: 100px;
    }

    &:hover {
        width: 220px;
        @media(max-width: 575px) {
            width: 110px;
        }
    }
}