.information {

	&-block {
		padding-top: 20px;
		font-size: 18px;

		@media(max-width: 1199px) {
			font-size: 14px;
		}

		.title-lg {
			margin-bottom: 2.3rem;
			margin-left: -200px;

			@media(max-width: 1199px) {
				font-size: 32px;
				margin-left: -150px;
			}
		}

		.title-md {
			@media(max-width: 1199px) {
				font-size: 19px;
			}
		}

		&-m {
			.title-md {
				@media(max-width: 767px) {
					font-size: 19px;
				}
				@media(max-width: 575px) {
					font-size: 14px;
					white-space: nowrap;
				}
			}

			.text-sm {
				line-height: 1.5;
			}

			@media(max-width: 991px) {
				.btn-default {
					max-width: 300px;
				}
				.btn-default {
					max-width: 300px;
				}
			}
		}
	}

	&-title {
		white-space: nowrap;
	}

	&-path {
	
		&-1 {
			flex: 0 0 22%;
			max-width: 22%;
			padding-top: 20px;
		}
	
		&-2 {
			flex: 0 0 41%;
			max-width: 41%;
			padding-top: 40px;
		}
	
		&-3 {
			flex: 0 0 37%;
			max-width: 37%;
			padding-top: 20px;
		}

	}

}