.counter {

	display: flex;
	flex-wrap: wrap;
	text-align: center;
	margin-left: -8px;
	font-family: 'Roboto Condensed';
	font-weight: 100;

	@media (max-width: 1199px) {
		margin-left: -25px;
	}
	@media (max-width: 991px) {
		margin-left: 0px;
		justify-content: center;
	}

	&-line {
		border-right: 2px solid $dark;
		margin-right: 5px;

		@media (max-width: 575px) {
			margin-right: 0;
		}
	}

	&-path {
		flex: 0 0 50px;
		max-width: 50px;

		@media (max-width: 1199px) {
			flex: 0 0 100px;
			max-width: 100px;
		}

		@media (max-width: 490px) {
			flex: 0 0 25%;
			max-width: 25%;
		}

	}
	
	&-num {
	    font-size: 36px;
	    line-height: 1;
	    @media (max-width: 1199px) {
	    	font-size: 52px;
	    }

	    @media (max-width: 575px) {
	    	font-size: 46px;
	    }
	}

	&-text {
		font-size: 14px;
		line-height: 1;
		font-weight: 300;
	}
}