.nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;

    &-link {
    	outline: none;
    	&:hover,&:focus {
    		color: $dark;
    		outline: none;
    	}
    }

    &-pills {
		width: 100%;
		border-bottom: 1px solid #ececec;
		margin-bottom: 25px;
		.nav-link {
			font-size: 19px;
			color: $dark;
			font-weight: 300;
			position: relative;
			flex: 0 0 25%;
			max-width: 25%;
			text-align: center;
			border-radius: 0;
			&.active {
				background:transparent;
				box-shadow: 0 4px 0 0 #7199ff;
				color: $dark;
			}

		}
    }

}