.cases {
	&-slide {
        padding-top: 0px;

        br {
            @media(max-width: 991px) {
                display: none;
            }
        }

        .title-md {
            font-size: 21px;
            height: 70px;
            display: flex;
            align-items: center;
            max-width: 70%;
            flex: 0 0 70%;
            margin: 0 auto;
            text-align: center;
            justify-content: center;
            line-height: 1;
        }
    }

    &-slider-section {
        

        .bx-controls-direction {
            width: 100%;
            padding-right: 0;
        }

        .bx-next, .bx-prev {
            width: 60px;
            height: 61px;
            top: 35px;
        }
    }

    &-slider--mobile {
        img {
            max-width: 200px;
            margin: 0 auto;
            display: block;
        }
    }
}
