.card {

    &-header {
        padding-bottom: 25px;
        padding-top: 25px;
        border-bottom: 1px solid #ebebeb;
        font-size: 24px;
        @media(max-width: 575px) {
            font-size: 17px;
            padding-bottom:15px;
            padding-top: 15px;
        }
        

        .btn-link {
            text-align: left;
            width: 100%;
            padding-right: 120px;
            position: relative;
            white-space: normal;
            &:before {
                content: '';
                display: block;
                width: 28px;
                height: 15px;
                background: url('../img/card-arrow.png') no-repeat/cover;
                position: absolute;
                right: 40px;
                top: 10px;
                z-index: -1;
                transform: rotate(180deg);

                @media(max-width: 575px) {
                    right: 0px;
                    top: 0px;
                }
            }

            @media(max-width: 575px) {
                padding-right: 60px;
            }

            &.collapsed {
                &:before {
                    transform: rotate(0deg);
                }
            }
        }
    }

    &-body {
        padding-bottom: 1.5rem;
        padding-top: 1.5rem;
        padding-right: 25px;

        @media(max-width: 575px) {
            font-size: 14px;
        }
    }
    
}


