.header {

	position: relative;
	z-index: 1;
	color: #fff;

	.bx-next, .bx-prev {
		z-index: 2;
	}


	&-slider {
		.slide-title {
			color: #fff;
			text-transform: uppercase;
			margin-bottom: 30px;
			font-weight: 400;
			letter-spacing: 0.5px;
			font-size: 48px;
			line-height: 1.2;
			@media (max-width:1199px) {
				font-size: 32px;
			}
		}

		.slide-text {
			color: #fff;
			font-size: 18px;
			letter-spacing: 0.5px;
			line-height: 1.55;
			@media (max-width:1199px) {
				font-size: 16px;
			}
		}
	}

	.bx-pager {
		padding: 20px;
		position: absolute;
		bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
	}

	.bx-pager-item {
		text-indent: -9999px;
		width: 10px;
		height: 10px;
		background: #fff;
		overflow: hidden;
		border-radius: 50%;
		margin: 0px 10px;
	}

	.bx-pager-link {
		display: block;
	}

	&-slide {
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 783px;
		height: 100vh;
		padding-top: 185px;
		padding-bottom: 85px;
		position: relative;
		z-index: 1;
		color: #fff;

		@media (max-width: 767px) {
			min-height: 100vh;
			padding-top: 85px;
			align-items: flex-start;
		}

		&-1 {
			background: url('../img/header-bg.jpg?v=1.1.26') 50% 50% / cover, #142139;
		}
		&-2 {
			background: linear-gradient(to right, #bb1f71, #4039ab);
		}
		&-3 {
			background: linear-gradient(to right, #4039ab, #bb1f71);
		}
		&-4 {
			background: linear-gradient(to right, #bb1f71, #4039ab);

		}
	}

	@media (max-width: 991px) {
		padding-top: 60px;
	}

	&-title {
		font-size: 40.5px;
		line-height: 1.2;
		text-transform: uppercase;

		@media (max-width: 1199px) {
			font-size: 35.5px;
		}

		@media (max-width: 575px) {
			font-size: 7vw;
		}

		img {
			max-width: 100%;
		}

		.line {

			&-1{}
			&-2{}
			&-3{
				letter-spacing: 1.3px;
				margin-bottom: 1.5rem;
                font-size: 36px;

                @media (max-width: 575px) {
			         font-size: 6vw;
		         }


				@media (max-width: 1199px) {
					margin-bottom: 1.5rem;
				}

			}

		}


	.line-1D{
	@media (max-width: 575px) {
			         font-size: 6vw;
		         }
	 font-size: 27px;
     margin-bottom: 15px;
	}

	.line-1D b{
	font-weight: 500;
	}

    .line-idex{
    @media (max-width: 575px) {
			         font-size: 6vw;
		         }
    font-size: 27px;
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: center;
    }

    .line-idex b{
    font-weight: 500;

    }

	}

	.btn {
		font-family: 'Roboto Condensed';
		font-size: 18px;
		font-weight: 300;
	}

	&-image {
		margin-top: -25px;

		 -webkit-perspective: 1200;
  perspective: 1200;
  -moz-transform: perspective(1200px);
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;

		img {
			max-width: 525px;
			position: relative;
			transition: all .3s;

			-webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
		}
	}

	.particles {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		z-index: 0;
	}



}

.payment {

font-size: 33px;


}

.payment .fab {

     margin-right: 10px;


}

.plashka {
	position: absolute;
	top:94px;
	background: linear-gradient(to left,#c8227d 0,#991a4e 100%);
	left: 0;
	z-index: 100;
	padding: 10px 0;
	width: 100%;
	text-align: center;
	font-size: 24px;
	line-height: 1;
	vertical-align: middle;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #ffffff;

	@media (max-width: 991px) {
		top: 0px;
	}

	img {
		height: 40px;
	}

	span {
		font-weight: bold;
		text-decoration: underline;
	}
}

.plashka-scroll {
	display: none;
	position: fixed;
	left: 0;
	@media (max-width: 991px) {
		top: 60px;
	}
}