.listing {
    
    &-item {
        display: flex;
        height: 90px;
        margin-bottom: 15px;
        text-align: center;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        @media(max-width: 991px) {
            height: 75px;
        }

        @media(max-width: 767px) {
            height: 65px;
        }

        @media(max-width: 575px) {
            height: 55px;
        }

        img {
            
            max-height: 63px;
            max-width: 90%;

            &.bright {
                filter: brightness(0);
                opacity: .5;
            }
        }
    }

}