.social-list {

    padding-left: 0;
    margin-bottom: 0px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 4rem;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 30px;

    @media(max-width: 1199px) {
        margin-bottom: 3rem;
    }
    @media(max-width: 991px) {
        margin-bottom: 0rem;
    }

    li {
        margin: 0 0px;
        @media(max-width: 991px) {
            flex: 0 0 13%;
            &:nth-child(-n+7) {
                margin-bottom: 15px;
            }
        }
    }

    img {
        height: 40px;
    }

    &--light {

        a {
            &:hover {
                img {
                    opacity: 1;
                }
            }
        }

        img {
            opacity: .4;
            height: 32px;
            transition: all .3s;
        }

    }

    &-vertical {
        flex-direction: column;
        margin-bottom: 0;
        padding-top: 0;
        height: 100%;
        @media(max-width: 991px) {
            flex-direction: row;
            background: #ececec;
        }

        li {
            margin-bottom: 30px;
            text-align: center;
            &:last-child {
                margin-bottom: 0;
            }
            @media(max-width: 991px) {
                margin-bottom: 20px!important;
                margin-top: 20px!important;
            }
        }

        img {
            max-width: 52px;
        }
    }

}