.roadmap-slider {
	margin-top: 50px;
	position: relative;
	z-index: 5;
	position: relative;
	&:before,
	&:after {
		content: '';
		height: 130%;
		position: absolute;
		top: -15%;
		width: 25vw;
		z-index: 3;
	}
	&:before {
		background: linear-gradient(to right, rgba(250, 250, 251, 1) 50%, rgba(250, 250, 251, 0) 100%);
		left: 0;
		@media(max-width:991px) {
			background: linear-gradient(to right, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
		}
	}
	&:after {
		background: linear-gradient(to left, rgba(250, 250, 251, 1) 50%, rgba(250, 250, 251, 0) 100%);
		right: 0;
		@media(max-width:991px) {
			background: linear-gradient(to left, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
		}
	}

	@media(max-width: 991px) {
		margin-top: 0;
	}

	.bx-controls-direction {
		margin-top: 10px;
    	padding: 0px 20px;
    	top: 0;
	}

	.bx-prev {
		background: url(../img/arrow-left.png) 50% 50%/cover no-repeat;
	}

	.bx-next {
		background: url(../img/arrow-right.png) 50% 50%/cover no-repeat;
	}
}

.roadmap-block {
	position: relative; // left: 0;
	width: 3000px;
	left: calc(25vw - 343px);
}

.roadmap-line {
	width: 100%;
	height: 65px;
	background: url('../img/roadmap-line-1.png') repeat-x -300px 50%;
	margin-top: -35px;
    margin-bottom: -30px;
}

.line-dot {
	display: inline-block;
	width: 32px;
	height: 32px;
	border-radius: 50%;
	border: 2px solid #87acff;
	position: relative;
	background: #fafafb;
	&:before {
		content: '';
		width: 22px;
		height: 22px;
		border-radius: 50%;
		background: #87acff;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -11px;
		margin-top: -11px;
	}
}

.line-row {
	display: flex;
	justify-content: flex-start;
}

.line-top {
	position: relative;	
	top: -12px;
	padding-left: 343px;
	align-items: flex-end;
}

.line-bottom {
	position: relative;
	align-items: flex-start;
	padding-left: 550px;
	top: 12px;
}

.line-small {
	width: 320px;
}

.line-big {
	width: 535px;
}

.line-point {
	margin: 0 10px;
	padding: 0px 25px;
	font-size: 15px;
	@media(max-width: 991px) {
		padding: 0 17vw;
		margin: 0;
	}
}

.slider-controls {
	// position: absolute;
	// width: 100%;
	// left: 0;
	// top: 50%;
	transform: translateY(-50%);
	// z-index: 5;
}

.slider-arrow {
	position: relative;
	background: transparent;
	border: none;
	cursor: pointer;
	&[data-event="prev"] {
		bottom: 0px;
	}
	&[data-event="next"] {
		top: 0px;
	}
}