.btn {
	border-radius: 4rem;
	line-height: 1;
	padding: 16px 35px;
	font-size: 16px;
	transition: all .3s;
	outline: none;
	border: none;
	white-space: pre-wrap;
	&:focus,
	&:hover {
		box-shadow: none;
		outline: none;
		outline: -webkit-focus-ring-color auto 0px;
	}
	@media (max-width: 575px) {
		font-size: 14px;
	}
	&-link {
		padding: 0;
		font-size: inherit;
		border-radius: 0;
		color: inherit;
		text-decoration: none;
		&:focus {
			text-decoration: none;
		}
	}
	&-sm {
		padding: 10px 35px;
		font-size: 12px;
	}
	&-lg {}
	&-long {
		text-align: center;
		width: 100%;
		max-width: 320px;
	}
	&-border-left {
		border-left: 1px solid #ffffff;
	}
	&-transparent {
		background: transparent;
		color: $white;
		letter-spacing: 1px;
		font-weight: 400;
		border: 1px solid #8C8680;
		&:hover,
		&:focus,
		&:active {
			background: $white;
			color: $dark;
		}
	}
	&-default {
		background: linear-gradient(to left, #c8227d 0%, #991a4e 100%);
		color: $white;
		letter-spacing: 1px;
		font-weight: 400;
		&:hover,
		&:focus,
		&:active {
			background-image: linear-gradient(to right, #c0e1fe 0%, #92b0ff 100%) !important;
		}
	}
	&-primary {
		background: linear-gradient(to right, #c0e1fe 0%, #92b0ff 100%);
		color: $white;
		letter-spacing: 1px;
		font-weight: 400;
		&:hover,
		&:focus,
		&:active {
			background-image: linear-gradient(to left, #c8227d 0%, #991a4e 100%) !important;
		}
	}
	&-group {
		.btn {
			padding: 16px 27px;
		}
	}
	&-fluid {
		width: 100%;
		text-align: center;
	}
	&-buy {
		background: #c8227d;
		box-shadow: 150px 0px 100px 0px #792144 inset, 0 0 0px 0px #fa54af;
		animation: background 3s infinite normal;
		color: $white;
		letter-spacing: 1px;
		font-weight: 400;
	}
	&-nowrap {
		    white-space: nowrap!important;
	}
}